.modal {
  display: none;
  width: 100vw;
  height: 100vh;
  min-height: 610px;
  position: absolute;
  top: 0;
  left: 0; }

.modal__call {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 490px;
  margin-left: -245px;
  margin-top: -303px;
  padding: 33px 39px 23px 39px;
  background-color: #00b2ff;
  z-index: 6; }

.modal__overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5; }

.modal--show {
  display: block; }

.modal__title {
  margin: 0;
  margin-bottom: 9px;
  padding: 0;
  font-family: "RobotoCondensed-Regular", "Arial", sans-serif;
  font-size: 28px;
  line-height: 39px;
  font-style: normal;
  font-weight: normal;
  color: #ffffff; }

.modal__text {
  width: 381px;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  color: #ffffff; }

.modal__close {
  position: absolute;
  top: 19px;
  right: 19px;
  width: 20px;
  height: 20px;
  border: none;
  background-color: transparent; }

.modal__close::before,
.modal__close::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 50%;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  transform: rotate(-45deg); }

.modal__close::after {
  transform: rotate(45deg); }

.modal__close:hover {
  opacity: 0.5;
  cursor: pointer; }

@media (max-width: 767px) {
  .modal__call {
    width: 320px;
    margin-left: -160px; }
  .modal__text {
    width: 100%; } }
