.header {
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-right: 45px;
}

.header__contacts-container {
  display: flex;
  align-items: center;
  max-width: 1170px;
  margin-left: auto;
  padding-top: 5px;
}

.header__menu {
  display: none;
}

.header__mail {
  position: relative;
  padding-left: 40px;
  margin-left: auto;
  line-height: 18px;
  background-image: url("../img/icon-envelope.svg");
  background-position: 0 center;
  background-repeat: no-repeat;
  color: #ffffff;
}

.header__tel {
  position: relative;
  margin-left: 88px;
  margin-right: 50px;
  padding-left: 34px;
  line-height: 18px;
  background-image: url("../img/icon-phone.svg");
  background-position: 0 center;
  background-repeat: no-repeat;
  color: #ffffff;
}

.header__tel:hover,
.header__mail:hover {
  opacity: 0.8;
  cursor: pointer;
}

@media (max-width: $desktop-width) {
  .header {
    padding: 0 24px;
    padding-top: 17px;
    padding-bottom: 13px;
  }

  .header__contacts-container {
    align-items: top;
    justify-content: space-between;
    max-width: none;
    min-width: 155px;
    padding-top: 0;
  }

  .header__menu {
    display: flex;
    width: 37px;
    height: 13px;
    border-top: 1px solid #ffffff;
    border-bottom: 1px solid #ffffff;
  }

  .header__menu:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  .header__mail {
    width: 25px;
    height: 18px;
    margin: 0;
    padding: 0;
    font-size: 0;
    background-position: center;
    background-size: cover;
  }

  .header__tel {
    width: 24px;
    height: 24px;
    margin: 0;
    padding: 0;
    font-size: 0;
    background-position: center;
    background-size: cover;
  }
}

@media (max-width: $tablet-width) {
  .header {
    padding-top: 17px;
    padding-right: 15px;
    padding-left: 17px;
  }

  .header__contacts-container {
    min-width: 137px;
    padding-bottom: 7px;
  }

  .header__menu {
    height: 13px;
  }
}
